.locationSection {
    padding: 32px 20px;
    text-align: center;
}

.content {
    max-width: 1200px;
    margin: 0 auto;
}

.title {
    font-size: 3rem;
    margin-bottom: 20px;
    opacity: 0;
    transform: translateY(20px);
    transition: opacity 0.8s ease, transform 0.8s ease;
}

.description {
    font-size: 1.5rem;
    max-width: 800px;
    margin: 0 auto 40px;
    opacity: 0;
    transform: translateY(20px);
    transition: opacity 0.8s ease 0.3s, transform 0.8s ease 0.3s;
}

.mapContainer {
    opacity: 0;
    transform: translateY(20px);
    transition: opacity 0.8s ease 0.6s, transform 0.8s ease 0.6s;
}

.locationSection.visible .title,
.locationSection.visible .description,
.locationSection.visible .mapContainer {
    opacity: 1;
    transform: translateY(0);
}

iframe {
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
